<template>
  <div class="columns is-centered m-2">
    <div class="column is-one-quarter">
      <form v-on:submit.prevent="onSubmit">
        <div class="field">
          <p class="control has-icons-left has-icons-right">
            <input
              class="input"
              type="email"
              placeholder="Email"
              v-model="email"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-envelope"></i>
            </span>
            <span class="icon is-small is-right">
              <i class="fas fa-check"></i>
            </span>
          </p>
        </div>
        <div class="field">
          <p class="control has-icons-left">
            <input
              class="input"
              type="password"
              placeholder="Password"
              v-model="password"
            />
            <span class="icon is-small is-left">
              <i class="fas fa-lock"></i>
            </span>
          </p>
        </div>
        <div class="field">
          <p class="control">
            <input type="submit" name="submit" class="button is-primary" />
            Login
          </p>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import ApiService from "../ApiService";
export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
    };
  },
  methods: {
    async onSubmit() {
      //Create new todo
      try {
        const res = await ApiService.login({
          email: this.email,
          password: this.password,
        });
        console.log(res);
      } catch (error) {
        console.log(error);
      }
      const notebook = JSON.parse(localStorage.getItem("notebook"));

      this.$router.push({
        path: `/${notebook.notebook_name}`,
      });
    },
    async getUser() {
      //Create new todo
      try {
        const res = await ApiService.getUser();
        console.log(res);
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
