<script>
import ApiService from "../ApiService";
export default {
  name: "HomePage",
  data() {
    return {
      isActive: false,
      showNavbar: true,
      error: "",
      notebook: {},
    };
  },
  beforeMount: function() {
    var notebook = JSON.parse(localStorage.getItem("notebook"));
    if (notebook) {
      this.$router.push({
        path: `/${notebook.notebook_name}`,
      });
    }
  },
  methods: {
    async createNotebook() {
      try {
        this.notebook = await ApiService.createNotebook();
        //set notebook in localstorage
        localStorage.setItem("notebook", JSON.stringify(this.notebook));

        this.$router.push({
          path: `/${this.notebook.notebook_name}`,
        });
      } catch (error) {
        this.error = error.message;
      }
    },
  },
};
</script>

