import axios from "axios";

const url = "/api/v1";

class ApiService {
  //create notebook
  static async createNotebook() {
    const res = await axios.post(`${url}/notebooks`);
    const data = res.data.data;
    return data;
  }
  static async getNotebook(notebookId) {
    const res = await axios.get(`${url}/notebooks/name/${notebookId}`);
    const data = res.data.data;
    return data;
  }
  static async getTodosByNotebookId(notebookId) {
    const res = await axios.get(`${url}/notebooks/${notebookId}/todos`);
    const data = res.data;
    return data;
  }
  static async createTodo(todo) {
    const res = await axios.post(
      `${url}/notebooks/${todo.notebook}/todos`,
      todo
    );
    const data = res.data.data;
    return data;
  }
  static async updateNotebookTitle(notebookId, title) {
    const res = await axios.put(`${url}/notebooks/${notebookId}/`, { title });
    const data = res.data.data;
    return data;
  }
  static async updateTodo(todoId, todo) {
    const res = await axios.put(`${url}/todos/${todoId}/`, todo);
    const data = res.data.data;
    return data;
  }
  static async deleteTodo(todoId) {
    const res = await axios.delete(`${url}/todos/${todoId}/`);
    const data = res.data;
    return data;
  }
  static async login(credentials) {
    const res = await axios.post(`${url}/auth/login`, credentials);
    const data = res.data;

    return data;
  }

  static async getUser() {
    const res = await axios.post(`${url}/auth/me`);
    const data = res.data.data;

    return data;
  }
}

export default ApiService;
